                    '.': enemy.value.stats.sexPower,
                    '.': enemy.value.stats.baseEndurance,
                    '.': enemy.value.stats.charm,
                    '.': enemy.value.stats.luck,
                    '.': enemy.value.stats.evasion,
                    '.': enemy.value.stats.crit,
                    '.': enemySkillCooldowns,
                    '.': combatItems,
