        /**
         *  debuff/buff  MVU 
         * 
         *  reloadStatusFromMvu 
         */
        async function applySkillEffectsFromMvu(skillId, isPlayerSkill) {
            const logs = [];
            console.info(`[Debuff] applySkillEffectsFromMvu: skillId=${skillId}, isPlayerSkill=${isPlayerSkill}`);
            try {
                if (typeof Mvu === 'undefined') {
                    console.warn('[] Mvu');
                    return logs;
                }
                const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                if (!mvuData?.stat_data) {
                    console.warn('[] MVU');
                    return logs;
                }
                // MVU
                const skillPath = isPlayerSkill
                    ? `..${skillId}..`
                    : `..${skillId}..`;
                const effectList = _.get(mvuData.stat_data, skillPath, {});
                if (!effectList || Object.keys(effectList).length === 0) {
                    return logs;
                }
                console.info(`[Debuff] : ${skillId}, isPlayerSkill=${isPlayerSkill}, =${skillPath}`, effectList);
                console.info(`[Debuff] keys:`, Object.keys(effectList));
                for (const [effectName, effectData] of Object.entries(effectList)) {
                    console.info(`[Debuff] : ${effectName}, effectData=${typeof effectData}, effectData=`, effectData);
                    if (!effectData || typeof effectData !== 'object') {
                        console.warn(`[Debuff] : ${effectName}`);
                        continue;
                    }
                    const effectType = _.get(effectData, '', '');
                    console.info(`[Debuff] : ${effectName}, =${effectType}`);
                    const effectValue = _.get(effectData, '', 0);
                    const isPercentage = _.get(effectData, '', false);
                    const duration = _.get(effectData, '', 0);
                    const targetEnemy = _.get(effectData, '', true);
                    // 
                    // effectValue0duration>0
                    if (effectType === '') {
                        console.info(`[] : duration=${duration}, targetEnemy=${targetEnemy}, isPlayerSkill=${isPlayerSkill}`);
                        if (duration === 0) {
                            console.warn(`[] duration0`);
                            continue;
                        }
                        const targetIsPlayer = isPlayerSkill ? !targetEnemy : targetEnemy;
                        console.info(`[] : targetIsPlayer=${targetIsPlayer}, isPlayerSkill=${isPlayerSkill}, targetEnemy=${targetEnemy}`);
                        if (targetIsPlayer) {
                            // 
                            let immuneToBind = false;
                            if (playerTalent.value) {
                                const talentContext = createTalentEffectContext();
                                const debuffResult = TalentSystem.processTalentOnDebuffReceived(playerTalent.value, talentContext, 'bind');
                                if (debuffResult.preventBind) {
                                    immuneToBind = true;
                                    logs.push(`${playerTalent.value.name}`);
                                    console.info(`[] `);
                                }
                            }
                            if (immuneToBind) {
                                continue; // 
                            }
                            // +2
                            let finalDuration = duration;
                            const sinTypeForBind = TalentSystem.getSinTalentType(playerTalent.value);
                            if (sinTypeForBind === 'greed') {
                                finalDuration += 2;
                                logs.push(`+2`);
                            }
                            playerBoundTurns.value = finalDuration;
                            playerBindSource.value = isPlayerSkill ? 'player' : 'enemy';
                            logs.push(`${player.value.name}  ${finalDuration} `);
                            console.info(`[]  : playerBoundTurns=${playerBoundTurns.value}`);
                        }
                        else {
                            // BOSS
                            if (BossSystem.bossState.isBossFight && BossSystem.bossState.bossId === 'muxinlan') {
                                const immuneDialogue = BossSystem.getBindImmuneDialogue(BossSystem.bossState.currentPhase);
                                if (immuneDialogue) {
                                    BossSystem.queueDialogues([immuneDialogue]);
                                }
                                logs.push(`${enemy.value.name} `);
                                console.info(`[] BOSS`);
                                continue;
                            }
                            enemyBoundTurns.value = duration;
                            enemyBindSource.value = isPlayerSkill ? 'player' : 'enemy';
                            logs.push(`${enemy.value.name}  ${duration} `);
                            console.info(`[]  : enemyBoundTurns=${enemyBoundTurns.value}, enemyBindSource=${enemyBindSource.value}`);
                        }
                        continue;
                    }
                    // effectValue0duration0
                    if (effectValue === 0 || duration === 0)
                        continue;
                    // 
                    const targetIsPlayer = isPlayerSkill ? !targetEnemy : targetEnemy;
                    const targetName = targetIsPlayer ? player.value.name : enemy.value.name;
                    const statusListPath = targetIsPlayer ? '.' : '..';
                    // key
                    const statusKey = `${effectType}_${skillId}_${effectName}`;
                    // 
                    const bonusObj = {};
                    const bonusFieldMap = {
                        : isPercentage ? '' : '',
                        : isPercentage ? '' : '',
                        : '',
                        : '',
                        : '',
                        : '',
                    };
                    const bonusField = bonusFieldMap[effectType];
                    if (!bonusField) {
                        console.warn(`[Debuff] : ${effectType}`);
                        continue;
                    }
                    bonusObj[bonusField] = effectValue; // 
                    // 
                    const currentStatusList = _.get(mvuData.stat_data, statusListPath, {});
                    // 
                    if (currentStatusList[statusKey]) {
                        // 
                        currentStatusList[statusKey]. = duration;
                        logs.push(`${targetName}  ${effectType}  (${duration} )`);
                        console.info(`[Debuff] : ${statusKey}`);
                    }
                    else {
                        // 
                        currentStatusList[statusKey] = {
                            : bonusObj,
                            : duration,
                        };
                        const sign = effectValue > 0 ? '+' : '';
                        logs.push(`${targetName} ${sign}${effectValue}${isPercentage ? '%' : ''} ${effectType} (${duration} )`);
                        console.info(`[Debuff] : ${statusKey}`, currentStatusList[statusKey]);
                    }
                    _.set(mvuData.stat_data, statusListPath, currentStatusList);
                }
                // 
                await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                //  ->  ->  -> UI
                await updateEnemyRealtimeStats();
            }
            catch (e) {
                console.error('[Debuff] ', e);
                logs.push('');
            }
            return logs;
        }
        /**
         * 
         * 
         * 
         */
        async function updateStatusEffectsFromMvu() {
            const logs = [];
            try {
                if (typeof Mvu === 'undefined')
                    return logs;
                const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                if (!mvuData?.stat_data)
                    return logs;
                // 
                const playerStatusList = _.get(mvuData.stat_data, '.', {});
                const updatedPlayerStatus = {};
                for (const [statusKey, statusData] of Object.entries(playerStatusList)) {
                    if (typeof statusData !== 'object' || statusData === null)
                        continue;
                    const currentDuration = statusData. || 0;
                    const newDuration = currentDuration - 1;
                    if (newDuration > 0) {
                        updatedPlayerStatus[statusKey] = { ...statusData, : newDuration };
                    }
                    else {
                        const effectType = statusKey.split('_')[0];
                        logs.push(`${player.value.name}  ${getEffectTypeName(effectType)} `);
                        console.info(`[Debuff] : ${statusKey}`);
                    }
                }
                _.set(mvuData.stat_data, '.', updatedPlayerStatus);
                // 
                const enemyStatusList = _.get(mvuData.stat_data, '..', {});
                const updatedEnemyStatus = {};
                for (const [statusKey, statusData] of Object.entries(enemyStatusList)) {
                    if (typeof statusData !== 'object' || statusData === null)
                        continue;
                    const currentDuration = statusData. || 0;
                    const newDuration = currentDuration - 1;
                    if (newDuration > 0) {
                        updatedEnemyStatus[statusKey] = { ...statusData, : newDuration };
                    }
                    else {
                        const effectType = statusKey.split('_')[0];
                        logs.push(`${enemy.value.name}  ${getEffectTypeName(effectType)} `);
                        console.info(`[Debuff] : ${statusKey}`);
                    }
                }
                _.set(mvuData.stat_data, '..', updatedEnemyStatus);
                await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                // 
                await updateEnemyRealtimeStats();
            }
            catch (e) {
                console.error('[Debuff] ', e);
            }
            return logs;
        }
        // 
        function getEffectTypeName(effectType) {
            const names = {
                : '',
                : '',
                : '',
                : '',
                : '',
            };
            return names[effectType] || effectType;
        }
        function getSkillDamageSourceLabel(skill) {
            const key = skill?.data?.damageSource || skill?.data?.damageFormula?.[0]?.source;
            const map = {
                sex_power: '',
                charm: '',
                luck: '',
                fixed: '',
                target_pleasure: '',
                willpower: '',
            };
            return map[key] || '';
        }
        function getSkillPowerCoeff(skill) {
            const v = skill?.data?.powerCoeff;
            if (typeof v === 'number' && Number.isFinite(v))
                return v;
            const coef = skill?.data?.damageFormula?.[0]?.coefficient;
            if (typeof coef === 'number' && Number.isFinite(coef))
                return Math.round(coef * 100);
            return 100;
        }
        function loadSkillData(skill) {
            if (!skill.data)
                skill.data = {};
            if (!skill.data.damageSource) {
                const source = skill.data.damageFormula?.[0]?.source;
                if (source)
                    skill.data.damageSource = source;
            }
            if (!skill.data.powerCoeff) {
                const coef = skill.data.damageFormula?.[0]?.coefficient;
                if (coef)
                    skill.data.powerCoeff = Math.round(coef * 100);
            }
        }
        //  mvuSchema.ts 
        function calculateBonusFromStatusList(statusList) {
            const totalBonus = {
                : 0,
                : 0,
                : 0,
                : 0,
                : 0,
                : 0,
                : 0,
                : 0,
            };
            for (const [_statusKey, statusData] of Object.entries(statusList)) {
                if (typeof statusData === 'object' && statusData !== null) {
                    const bonus = statusData. || {};
                    for (const [key, value] of Object.entries(bonus)) {
                        if (typeof value === 'number' && key in totalBonus) {
                            totalBonus[key] += value;
                        }
                    }
                }
            }
            return totalBonus;
        }
        /**
         *  MVU  UI
         *  ->  ->  -> += ->  -> UI
         */
        async function updateEnemyRealtimeStats() {
            try {
                if (typeof Mvu === 'undefined')
                    return;
                const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                if (!mvuData?.stat_data)
                    return;
                const data = mvuData.stat_data;
                // 1. 
                const enemyStatusList = _.get(data, '..', {});
                const enemyTempBonus = calculateBonusFromStatusList(enemyStatusList);
                //  StatusEffect[]
                const enemyEffects = [];
                Object.entries(enemyStatusList).forEach(([name, val]) => {
                    let duration = 0;
                    let bonuses = {};
                    if (typeof val === 'number') {
                        duration = val;
                    }
                    else if (typeof val === 'object' && val !== null) {
                        duration = _.get(val, '', 0);
                        bonuses = _.get(val, '', {});
                    }
                    if (duration <= 0)
                        return;
                    let displayName = '';
                    let type = 'buff';
                    let icon = '';
                    if (Object.keys(bonuses).length > 0) {
                        const bonusTexts = [];
                        for (const [key, value] of Object.entries(bonuses)) {
                            if (value === 0)
                                continue;
                            const attrMap = {
                                : '',
                                : '',
                                : '',
                                : '',
                                : '',
                                : '%',
                                : '',
                                : '%',
                            };
                            const attrName = attrMap[key] || key;
                            const sign = value > 0 ? '+' : '';
                            bonusTexts.push(`${attrName}${sign}${value}`);
                            if (value < 0)
                                type = 'debuff';
                        }
                        displayName = bonusTexts.join(', ');
                    }
                    else {
                        displayName = name;
                    }
                    if (type === 'debuff')
                        icon = '';
                    else
                        icon = '';
                    if (name.includes(''))
                        icon = '';
                    if (name.includes(''))
                        icon = '';
                    if (name.includes(''))
                        icon = '';
                    if (name.includes(''))
                        icon = '';
                    if (name.includes(''))
                        icon = '';
                    if (name.includes(''))
                        icon = '';
                    enemyEffects.push({
                        id: 'enemy_' + name,
                        name: displayName || name,
                        duration: duration,
                        icon: icon,
                        type: type,
                        effect: { type: 'focus', value: 0, isPercent: false, duration: duration, stackable: false },
                    });
                });
                enemy.value.statusEffects = enemyEffects;
                // 2.  MVU
                _.set(data, '..', enemyTempBonus);
                // 3. 
                const baseCharm = _.get(data, '.', 0);
                const baseLuck = _.get(data, '.', 0);
                const baseEvasion = _.get(data, '.', 0);
                const baseCrit = _.get(data, '.', 0);
                const baseSexPower = _.get(data, '.', 0);
                const baseEndurance = _.get(data, '.', 0);
                // 4.  =  + 
                const realtimeCharm = Math.max(0, baseCharm + (enemyTempBonus. || 0));
                const realtimeLuck = Math.max(0, baseLuck + (enemyTempBonus. || 0));
                const realtimeEvasion = Math.min(60, Math.max(0, baseEvasion + (enemyTempBonus. || 0)));
                const realtimeCrit = Math.min(100, Math.max(0, baseCrit + (enemyTempBonus. || 0)));
                // 
                const sexPowerBonus = enemyTempBonus. || 0;
                const sexPowerMultiplier = (enemyTempBonus. || 0) / 100;
                const realtimeSexPower = Math.max(0, Math.round((baseSexPower + sexPowerBonus) * (1 + sexPowerMultiplier)));
                const enduranceBonus = enemyTempBonus. || 0;
                const enduranceMultiplier = (enemyTempBonus. || 0) / 100;
                const realtimeEndurance = Math.max(0, Math.round((baseEndurance + enduranceBonus) * (1 + enduranceMultiplier)));
                // 5.  MVU
                _.set(data, '.', realtimeCharm);
                _.set(data, '.', realtimeLuck);
                _.set(data, '.', realtimeEvasion);
                _.set(data, '.', realtimeCrit);
                _.set(data, '.', realtimeSexPower);
                _.set(data, '.', realtimeEndurance);
                await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                // 6.  UI
                enemy.value.stats.charm = realtimeCharm;
                enemy.value.stats.luck = realtimeLuck;
                enemy.value.stats.evasion = realtimeEvasion;
                enemy.value.stats.crit = realtimeCrit;
                enemy.value.stats.sexPower = realtimeSexPower;
                enemy.value.stats.baseEndurance = realtimeEndurance;
                console.info(`[Debuff]  - :${baseSexPower}->${realtimeSexPower}, :${baseEndurance}->${realtimeEndurance}`);
            }
            catch (e) {
                console.error('[Debuff] ', e);
            }
        }
