                const playerTalentBonus = {};
                if (playerTalent.value?.bonus) {
                    const tb = playerTalent.value.bonus;
                    if (tb.)
                        playerTalentBonus[''] = tb.;
                    if (tb.)
                        playerTalentBonus[''] = tb.;
                    if (tb.)
                        playerTalentBonus[''] = tb.;
                    if (tb.)
                        playerTalentBonus[''] = tb.;
                    if (tb.)
                        playerTalentBonus[''] = tb.;
                    if (tb.)
                        playerTalentBonus[''] = tb.;
                    if (tb.)
                        playerTalentBonus[''] = tb.;
                    if (tb.)
                        playerTalentBonus[''] = tb.;
                }
                // 
                _.set(data, '.', playerTempBonus);
                // 
                const playerBaseSexPower = _.get(data, '.$', 10);
                const playerSexPowerBonus = (playerTempBonus. || 0) +
                    (playerPermBonus. || 0) +
                    (playerEquipBonus. || 0) +
                    (playerTalentBonus[''] || 0);
                const playerSexPowerMultiplier = ((playerTempBonus. || 0) +
                    (playerPermBonus. || 0) +
                    (playerEquipBonus. || 0) +
                    (playerTalentBonus[''] || 0)) /
                    100;
                const calculatedSexPower = Math.max(0, Math.round((playerBaseSexPower + playerSexPowerBonus) * (1 + playerSexPowerMultiplier)));
                player.value.stats.sexPower = calculatedSexPower;
                // 
                const playerBaseEndurance = _.get(data, '.$', 10);
                const playerEnduranceBonus = (playerTempBonus. || 0) +
                    (playerPermBonus. || 0) +
                    (playerEquipBonus. || 0) +
                    (playerTalentBonus[''] || 0);
                const playerEnduranceMultiplier = ((playerTempBonus. || 0) +
                    (playerPermBonus. || 0) +
                    (playerEquipBonus. || 0) +
                    (playerTalentBonus[''] || 0)) /
                    100;
                const calculatedEndurance = Math.max(0, Math.round((playerBaseEndurance + playerEnduranceBonus) * (1 + playerEnduranceMultiplier)));
                player.value.stats.baseEndurance = calculatedEndurance;
                // 
                _.set(data, '.', calculatedSexPower);
                _.set(data, '.', calculatedEndurance);
                // 0
                player.value.stats.charm = Math.max(0, _.get(data, '.$', 10) +
                    (playerTempBonus. || 0) +
                    (playerPermBonus. || 0) +
                    (playerEquipBonus. || 0) +
                    (playerTalentBonus[''] || 0));
                player.value.stats.luck = Math.max(0, _.get(data, '.$', 10) +
                    (playerTempBonus. || 0) +
                    (playerPermBonus. || 0) +
                    (playerEquipBonus. || 0) +
                    (playerTalentBonus[''] || 0));
                player.value.stats.evasion = Math.min(60, Math.max(0, _.get(data, '.$', 0) +
                    (playerTempBonus. || 0) +
                    (playerPermBonus. || 0) +
                    (playerEquipBonus. || 0) +
                    (playerTalentBonus[''] || 0)));
                player.value.stats.crit = Math.min(100, Math.max(0, _.get(data, '.$', 0) +
                    (playerTempBonus. || 0) +
                    (playerPermBonus. || 0) +
                    (playerEquipBonus. || 0) +
                    (playerTalentBonus[''] || 0)));
                // 
                player.value.stats.currentPleasure = _.get(data, '.$', 0);
                player.value.stats.maxPleasure = _.get(data, '.$', 100);
                player.value.stats.climaxCount = _.get(data, '.', 0);
                enemy.value.stats.currentPleasure = _.get(data, '.', 0);
                enemy.value.stats.maxPleasure = _.get(data, '.', 100);
                enemy.value.stats.climaxCount = _.get(data, '.', 0);
                await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                // === ===
                await updateEnemyRealtimeStats();
            }
            catch (e) {
                console.error('[] ', e);
            }
        }
        // 
        function triggerEffect(type) {
            effectType.value = type;
            showEffect.value = true;
            setTimeout(() => {
                showEffect.value = false;
                setTimeout(() => {
                    effectType.value = null;
                }, 300);
            }, 1500);
        }
        // 
