                _.set(mvuData.stat_data, '.', {});
                _.set(mvuData.stat_data, '.', {});
                _.set(mvuData.stat_data, '.', {});
                // 
                _.set(mvuData.stat_data, '.', 0);
                _.set(mvuData.stat_data, '.', 0);
                //  saveToMvu  MVU
                player.value.stats.climaxCount = 0;
                enemy.value.stats.climaxCount = 0;
                // 
                // _.set(mvuData.stat_data, '.', '');
                // _.set(mvuData.stat_data, '.', 0);
