        }
        function addLog(message, source, type = 'info') {
            const logEntry = {
                id: Math.random().toString(36).substr(2, 9),
                turn: turnState.currentTurn,
                message,
                source,
                type,
            };
            logs.value.push(logEntry);
            // MVUUI
            (async () => {
                try {
                    if (typeof Mvu !== 'undefined') {
                        const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                        if (mvuData) {
                            const logKey = `${turnState.currentTurn}_${Date.now()}`;
                            const logValue = `[${logEntry.turn}] ${logEntry.source === 'player' ? player.value.name : logEntry.source === 'enemy' ? enemy.value.name : ''}: ${logEntry.message}`;
                            const currentLogs = _.get(mvuData.stat_data, '.', {});
                            const updatedLogs = { ...currentLogs, [logKey]: logValue };
                            _.set(mvuData.stat_data, '.', updatedLogs);
                            // UI
                            await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                        }
                    }
                }
                catch (e) {
                    console.warn('[] ', e);
                }
            })();
        }
        function cloneCharacter(char) {
            return {
                ...char,
                stats: { ...char.stats },
                skills: char.skills.map(s => ({ ...s })),
                items: char.items.map(i => ({ ...i })),
                statusEffects: [...char.statusEffects],
            };
        }
        function getPhaseText(phase) {
            const texts = {
                playerInput: '',
                processing: '',
                enemyAction: '',
                victory: '',
                defeat: '',
                climaxResolution: '',
            };
            return texts[phase];
        }
        function isSkillDisabled(skill) {
            return skill.currentCooldown > 0 || player.value.stats.currentEndurance < skill.cost;
        }
        // =================  =================
        function syncEnemySkillsFromMvuIfNeeded() {
            try {
                if (typeof Mvu === 'undefined')
                    return;
                const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                if (!mvuData?.stat_data)
                    return;
                const mvuEnemySkills = _.get(mvuData.stat_data, '.', {}) || {};
                const mvuSkillIds = Object.keys(mvuEnemySkills);
                const currentIds = (enemy.value.skills || []).map(s => s.id).filter(Boolean);
                const normalize = (ids) => ids.slice().sort();
                const same = JSON.stringify(normalize(mvuSkillIds)) === JSON.stringify(normalize(currentIds));
                // MVU
                if (mvuSkillIds.length === 0) {
                    if (currentIds.length > 0) {
                        console.info('[] MVU');
                    }
                    enemy.value.skills = [];
                    turnState.enemyIntention = null;
                    return;
                }
                // MVUskillDatabase
                if (!same) {
                    console.info('[] MVUMVU');
                    enemy.value.skills = mvuSkillIds
                        .map((skillId) => {
                        const mvuSkill = mvuEnemySkills[skillId];
                        if (!mvuSkill?.)
                            return null;
                        return {
                            id: skillId,
                            name: mvuSkill.?. || skillId,
                            description: mvuSkill.?. || '',
                            cost: mvuSkill.?. || 0,
                            type: 'attack',
                            cooldown: mvuSkill.?. || 0,
                            currentCooldown: _.get(mvuData.stat_data, `..${skillId}`, 0),
                            data: null,
                        };
                    })
                        .filter((s) => s !== null);
                    // 
                    const refreshedIds = new Set((enemy.value.skills || []).map(s => s.id));
                    if (turnState.enemyIntention && !refreshedIds.has(turnState.enemyIntention.id)) {
                        turnState.enemyIntention = null;
                    }
                }
            }
            catch (e) {
                console.warn('[] ', e);
            }
        }
        function determineEnemyIntention() {
            // MVU
            syncEnemySkillsFromMvuIfNeeded();
            // 
            //  . 
            if (enemy.value.skills.length === 0) {
                console.warn('[] ');
                turnState.enemyIntention = null;
                return;
            }
            // ID
            const validSkills = enemy.value.skills.filter(s => {
                // ID
                if (!s.id || !s.name) {
                    console.warn('[] :', s);
                    return false;
                }
                return true;
            });
            if (validSkills.length === 0) {
                console.warn('[] ');
                turnState.enemyIntention = null;
                return;
            }
            // 
            const availableSkills = validSkills.filter(s => s.currentCooldown === 0);
            const skillsToChoose = availableSkills.length > 0 ? availableSkills : validSkills;
            if (skillsToChoose.length === 0) {
                console.warn('[] ');
                turnState.enemyIntention = null;
                return;
            }
            const skill = skillsToChoose[Math.floor(Math.random() * skillsToChoose.length)];
            console.info('[] :', skill.name, 'ID:', skill.id);
            turnState.enemyIntention = skill;
        }
        function handlePlayerSkill(skill) {
            if (turnState.phase !== 'playerInput')
                return;
            // handleEnemyTurn
            // 
            if (playerBoundTurns.value > 0) {
                addLog(`${player.value.name}  ${playerBoundTurns.value} `, 'system', 'info');
                return;
            }
            // ========== -3 ==========
            const sinType = TalentSystem.getSinTalentType(playerTalent.value);
            if (sinType === 'sloth' && playerTalentState.value.slothCannotAttackTurns > 0) {
                addLog(`3${playerTalentState.value.slothCannotAttackTurns}`, 'system', 'critical');
                return;
            }
            // ========== BOSSdebuff ==========
            let requiredCost = skill.cost;
            if (BossSystem.bossState.isBossFight && BossSystem.bossState.bossId === 'eden') {
                const slothEffects = BossSystem.getEdenSlothEffects();
                requiredCost = Math.floor(skill.cost * slothEffects.staminaCostMultiplier);
            }
            // 
            if (player.value.stats.currentEndurance < requiredCost) {
                addLog(` ${requiredCost} `, 'system', 'info');
                return;
            }
            if (isSkillDisabled(skill)) {
                addLog(skill.currentCooldown > 0 ? '' : '', 'system', 'info');
                return;
            }
            turnState.phase = 'processing';
            const nextPlayer = cloneCharacter(player.value);
            const nextEnemy = cloneCharacter(enemy.value);
            // 
            let actualCost = skill.cost;
            // ========== BOSS - 1.5 ==========
            if (BossSystem.bossState.isBossFight && BossSystem.bossState.bossId === 'eden') {
                const slothEffects = BossSystem.getEdenSlothEffects();
                const originalCost = actualCost;
                actualCost = Math.floor(actualCost * slothEffects.staminaCostMultiplier);
                if (actualCost > originalCost) {
                    addLog(`${originalCost}  ${actualCost}`, 'system', 'debuff');
                }
            }
            if (playerTalent.value) {
                const staminaCap = TalentSystem.getTalentStaminaChangeCap(playerTalent.value);
                if (staminaCap !== null && actualCost > staminaCap) {
                    addLog(`${playerTalent.value.name}${staminaCap}`, 'system', 'info');
                    actualCost = staminaCap;
                }
            }
            nextPlayer.stats.currentEndurance -= actualCost;
            addLog(`${nextPlayer.name}  ${actualCost} `, 'system', 'info');
            // 
            const skillIndex = nextPlayer.skills.findIndex(s => s.id === skill.id);
            if (skillIndex !== -1) {
                let finalCooldown = skill.cooldown || 0;
                // ========== BOSS - +2 ==========
                if (BossSystem.bossState.isBossFight && BossSystem.bossState.bossId === 'eden') {
                    const slothEffects = BossSystem.getEdenSlothEffects();
                    const originalCooldown = finalCooldown;
                    finalCooldown += slothEffects.cooldownIncrease;
                    addLog(`${originalCooldown}  ${finalCooldown}`, 'system', 'debuff');
                }
                nextPlayer.skills[skillIndex].currentCooldown = finalCooldown;
                if (finalCooldown > 0) {
                    addLog(`${skill.name}  ${finalCooldown} `, 'system', 'info');
                }
            }
            // 
            import('./combatCalculator').then(async ({ executeAttack, applySkillBuffs }) => {
                try {
                    // 
                    if (!skill.data) {
                        addLog(` ${skill.name} `, 'system', 'critical');
                        turnState.phase = 'playerInput';
                        return;
                    }
                    // ==========  ==========
                    let talentAttackResult = {};
                    let critDamageBoost = 0;
                    let sinExtraHits = 0;
                    let sinGuaranteedCrit = false;
                    let sinGuaranteedHit = false;
                    if (playerTalent.value) {
                        const hasBindEffect = skill.data.buffs?.some((e) => e.type === 'bind') || false;
                        const talentContext = createTalentEffectContext();
                        talentAttackResult = TalentSystem.processTalentOnAttack(playerTalent.value, talentContext, hasBindEffect);
                        // on_crit
                        for (const effect of playerTalent.value.effects) {
                            if (effect.trigger === 'on_crit' && effect.effect === 'boost_crit_damage') {
                                critDamageBoost = effect.params.value || 25;
                            }
                        }
                        // ==========  ==========
                        const sinType = TalentSystem.getSinTalentType(playerTalent.value);
                        if (sinType) {
                            switch (sinType) {
                                case 'wrath': {
                                    // +1
                                    const wrathMods = TalentSystem.getWrathModifiers(playerTalentState.value);
                                    if (wrathMods.extraHitCount) {
                                        sinExtraHits = wrathMods.extraHitCount;
                                        addLog(`+${sinExtraHits}`, 'system', 'critical');
                                    }
                                    break;
                                }
                                case 'sloth': {
                                    // 
                                    const slothMods = TalentSystem.getSlothAttackModifiers(playerTalentState.value, turnState.currentTurn);
                                    if (slothMods.guaranteedCrit)
                                        sinGuaranteedCrit = true;
                                    if (slothMods.guaranteedHit)
                                        sinGuaranteedHit = true;
                                    if (slothMods.extraHitCount)
                                        sinExtraHits = slothMods.extraHitCount;
                                    // 
                                    if (playerTalentState.value.slothStacks > 0) {
                                        const stacks = playerTalentState.value.slothStacks;
                                        addLog(`${stacks}${stacks >= 1 ? '' : ''}${stacks >= 2 ? '' : ''}${stacks >= 3 ? '+2' : ''}`, 'system', 'info');
                                        playerTalentState.value.slothStacks = 0;
                                        // buff
                                        removeTalentBuff('player', '__');
                                    }
                                    // 2-20%-15%
                                    playerTalentState.value.slothDebuffTurns = 2;
                                    applyTalentBuff('player', '__', { : -20, : -15 }, 2);
                                    addLog(`2-20%-15%`, 'system', 'critical');
                                    break;
                                }
                                case 'gluttony': {
                                    // 
                                    playerTalentState.value.gluttonyDealtDamageThisTurn = true;
                                    break;
                                }
                                case 'wrath': {
                                    // 
                                    playerTalentState.value.wrathDealtDamageThisTurn = true;
                                    break;
                                }
                                case 'pride': {
                                    // 2
                                    if (playerTalentState.value.prideAbsoluteConfidence) {
                                        sinGuaranteedHit = true;
                                        sinExtraHits = 2;
                                        addLog(`+2`, 'system', 'buff');
                                        playerTalentState.value.prideAbsoluteConfidence = false;
                                        playerTalentState.value.prideConsecutiveCrits = 0;
                                    }
                                    break;
                                }
                                case 'greed': {
                                    // 3150%300%
                                    if (playerTalentState.value.greedStacks >= 3) {
                                        critDamageBoost = 150; // 150%300%+150%
                                        addLog(`3300%`, 'system', 'buff');
                                    }
                                    break;
                                }
                            }
                        }
                    }
                    // ========== BOSS300% ==========
                    let edenCritBoost = 0;
                    if (BossSystem.bossState.isBossFight && BossSystem.bossState.bossId === 'eden') {
                        // 300% = 150% + 150%critDamageBoost
                        edenCritBoost = 150;
                        addLog(`300%`, 'system', 'critical');
                    }
                    const result = executeAttack(nextPlayer, nextEnemy, skill.data, true, {
                        guaranteedHit: talentAttackResult.guaranteedHit || sinGuaranteedHit,
                        guaranteedCrit: sinGuaranteedCrit,
                        damageMultiplier: talentAttackResult.damageMultiplier,
                        critDamageBoost: critDamageBoost + edenCritBoost,
                        extraHitCount: sinExtraHits,
                    }); // 
                    // ==========  ==========
                    if (playerTalent.value && result.totalDamage > 0 && !result.isDodged) {
                        const passiveModifiers = TalentSystem.getTalentPassiveModifiers(playerTalent.value, {
                            playerPleasure: nextPlayer.stats.currentPleasure,
                            playerMaxPleasure: nextPlayer.stats.maxPleasure,
                            playerStamina: nextPlayer.stats.currentEndurance,
                            playerMaxStamina: nextPlayer.stats.maxEndurance,
                            enemyPleasure: nextEnemy.stats.currentPleasure,
                            enemyMaxPleasure: nextEnemy.stats.maxPleasure,
                        });
                        console.info(`[] : =${nextPlayer.stats.currentPleasure}/${nextPlayer.stats.maxPleasure}, =${nextPlayer.stats.currentEndurance}/${nextPlayer.stats.maxEndurance}`);
                        console.info(`[] : damageBoostPercent=${passiveModifiers.damageBoostPercent}, powerCoeffBoost=${passiveModifiers.powerCoeffBoost}`);
                        // powerCoeffBoost
                        if (passiveModifiers.damageBoostPercent > 0 || passiveModifiers.powerCoeffBoost > 0) {
                            const boostPercent = passiveModifiers.damageBoostPercent + passiveModifiers.powerCoeffBoost;
                            const boostedDamage = Math.floor(result.totalDamage * (1 + boostPercent / 100));
                            const extraDamage = boostedDamage - result.totalDamage;
                            if (extraDamage > 0) {
                                result.totalDamage = boostedDamage;
                                addLog(`${playerTalent.value.name}${boostPercent}%+${extraDamage}`, 'system', 'info');
                            }
                        }
                    }
                    // 
                    addLog(`${nextPlayer.name}  ${skill.name}`, 'player', 'info');
                    // ==========  -  ==========
                    if (BossSystem.bossState.isBossFight &&
                        BossSystem.bossState.bossId === 'elizabeth' &&
                        BossSystem.bossState.elizabethCurrentCommand) {
                        // MVU
                        let skillRarity = 'C'; // C
                        if (typeof Mvu !== 'undefined') {
                            const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                            const skillMvuData = _.get(mvuData?.stat_data, `..${skill.id}`, null);
                            if (skillMvuData?.?.) {
                                skillRarity = skillMvuData..;
                            }
                        }
                        // 
                        const obedienceResult = BossSystem.checkElizabethCommandObedience('skill', skillRarity, result.isCritical);
                        // 
                        if (obedienceResult.dialogues.length > 0) {
                            BossSystem.queueDialogues(obedienceResult.dialogues);
                        }
                        if (obedienceResult.obeyed) {
                            addLog(`${skillRarity}`, 'system', 'info');
                        }
                        else {
                            // 20%
                            const staminaPenalty = Math.floor(nextPlayer.stats.maxEndurance * 0.2);
                            nextPlayer.stats.currentEndurance = Math.max(0, nextPlayer.stats.currentEndurance - staminaPenalty);
                            // 
                            const currentCommand = BossSystem.bossState.elizabethCurrentCommand;
                            const commandName = currentCommand === 'kneel' ? '' : '';
                            addLog(`${commandName}-${staminaPenalty}`, 'system', 'critical');
                            // BOSSbuff
                            const bonus = BossSystem.getElizabethViolationBonus();
                            addLog(`+${bonus.sexPowerBonus}+${bonus.enduranceBonus}+${bonus.evasionBonus}%+${bonus.critBonus}%`, 'system', 'buff');
                            // MVU
                            if (typeof Mvu !== 'undefined') {
                                const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                                if (mvuData?.stat_data) {
                                    _.set(mvuData.stat_data, '...', {
                                        : {
                                            : bonus.sexPowerBonus,
                                            : bonus.enduranceBonus,
                                            : bonus.evasionBonus,
                                            : bonus.critBonus,
                                        },
                                        : 999,
                                    });
                                    // BOSS-60%-90%2
                                    if (obedienceResult.punishBoss) {
                                        const critDebuff = BossSystem.getElizabethCritCounterDebuff();
                                        addLog(`${critDebuff.evasionDebuff}%${critDebuff.enduranceCalcDebuff}%2`, 'system', 'critical');
                                        _.set(mvuData.stat_data, '...', {
                                            : {
                                                : critDebuff.evasionDebuff,
                                                : critDebuff.enduranceCalcDebuff,
                                            },
                                            : 2,
                                        });
                                    }
                                    Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                                }
                            }
                        }
                    }
                    if (result.isDodged) {
                        addLog(`${nextEnemy.name} `, 'system', 'info');
                        triggerEffect('dodge');
                        // /
                        const sinTypeOnDodge = TalentSystem.getSinTalentType(playerTalent.value);
                        if (sinTypeOnDodge === 'gluttony') {
                            playerTalentState.value.gluttonyDealtDamageThisTurn = false;
                        }
                        if (sinTypeOnDodge === 'wrath') {
                            playerTalentState.value.wrathDealtDamageThisTurn = false;
                        }
                    }
                    else {
                        // 
                        console.info('[]  - result.logs:', result.logs);
                        if (result.logs && result.logs.length > 0) {
                            result.logs.forEach(log => {
                                addLog(log, 'system', 'info');
                            });
                        }
                        else {
                            console.warn('[]  - result.logs ');
                        }
                        // totalDamageactualDamage
                        if (result.isCritical) {
                            addLog(` ${result.totalDamage} `, 'player', 'critical');
                            triggerEffect('critical');
                            // ========== - ==========
                            const sinTypeForCrit = TalentSystem.getSinTalentType(playerTalent.value);
                            if (sinTypeForCrit === 'pride') {
                                playerTalentState.value.prideCritThisTurn = true;
                            }
                            // ========== BOSSdebuff (-40, -40, +4) ==========
                            if (BossSystem.bossState.isBossFight && BossSystem.bossState.bossId === 'eden') {
                                const critDebuffResult = BossSystem.processEdenCritReceived();
                                addLog(`+${critDebuffResult.countdownIncrease}: ${BossSystem.bossState.edenCountdown}`, 'system', 'critical');
                                addLog(`${critDebuffResult.evasionDebuff}%${critDebuffResult.critDebuff}%`, 'system', 'debuff');
                                // MVUdebuff (-40, -40)
                                if (typeof Mvu !== 'undefined') {
                                    const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                                    if (mvuData?.stat_data) {
                                        // 
                                        const currentEvasionDebuff = _.get(mvuData.stat_data, '...debuff..', 0);
                                        const currentCritDebuff = _.get(mvuData.stat_data, '...debuff..', 0);
                                        // debuff
                                        _.set(mvuData.stat_data, '...debuff', {
                                            : {
                                                : currentEvasionDebuff + critDebuffResult.evasionDebuff,
                                                : currentCritDebuff + critDebuffResult.critDebuff,
                                            },
                                            : 999, // 
                                        });
                                        // 
                                        const totalEvasion = _.get(mvuData.stat_data, '...', 0) +
                                            critDebuffResult.evasionDebuff;
                                        const totalCrit = _.get(mvuData.stat_data, '...', 0) +
                                            critDebuffResult.critDebuff;
                                        _.set(mvuData.stat_data, '...', totalEvasion);
                                        _.set(mvuData.stat_data, '...', totalCrit);
                                        // 
                                        const baseEvasion = _.get(mvuData.stat_data, '.', 0);
                                        const baseCrit = _.get(mvuData.stat_data, '.', 0);
                                        _.set(mvuData.stat_data, '.', Math.max(0, baseEvasion + totalEvasion));
                                        _.set(mvuData.stat_data, '.', Math.max(0, baseCrit + totalCrit));
                                        Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                                        // UIcritcritChance
                                        nextEnemy.stats.evasion = Math.max(0, nextEnemy.stats.evasion + critDebuffResult.evasionDebuff);
                                        nextEnemy.stats.crit = Math.max(0, nextEnemy.stats.crit + critDebuffResult.critDebuff);
                                    }
                                }
                            }
                        }
                        else {
                            addLog(` ${result.totalDamage} `, 'player', 'damage');
                        }
                        // - totalDamage
                        const oldPleasure = nextEnemy.stats.currentPleasure;
                        nextEnemy.stats.currentPleasure = Math.min(nextEnemy.stats.maxPleasure, nextEnemy.stats.currentPleasure + result.totalDamage);
                        addLog(`${nextEnemy.name}  ${oldPleasure}  ${nextEnemy.stats.currentPleasure}`, 'system', 'info');
                        // ==========  ==========
                        if (playerTalent.value && result.totalDamage > 0) {
                            const talentContext = createTalentEffectContext();
                            TalentSystem.processTalentOnDamageDealt(playerTalent.value, talentContext, result.totalDamage);
                            // ==========  ==========
                            const sinType = TalentSystem.getSinTalentType(playerTalent.value);
                            // 20%
                            if (sinType === 'gluttony') {
                                const pleasureReduce = Math.floor(nextPlayer.stats.maxPleasure * 0.2);
                                nextPlayer.stats.currentPleasure = Math.max(0, nextPlayer.stats.currentPleasure - pleasureReduce);
                                playerTalentState.value.gluttonyDealtDamageThisTurn = true;
                                addLog(`-${pleasureReduce}`, 'system', 'buff');
                            }
                            // 
                            if (sinType === 'wrath') {
                                playerTalentState.value.wrathDealtDamageThisTurn = true;
                            }
                            // 
                            if (sinType === 'lust') {
                                const charmResult = TalentSystem.processLustCharm(talentContext, nextPlayer.stats.charm, nextEnemy.stats.charm);
                                playerTalentState.value = { ...talentContext.talentState };
                                addLog(`${charmResult.message}`, 'system', charmResult.success ? 'info' : 'critical');
                                if (charmResult.success && charmResult.bindEnemy) {
                                    // 1
                                    if (BossSystem.bossState.isBossFight && BossSystem.bossState.bossId === 'muxinlan') {
                                        addLog(`${nextEnemy.name} `, 'system', 'info');
                                    }
                                    else if (enemyBoundTurns.value === 0) {
                                        enemyBoundTurns.value = charmResult.bindDuration || 1;
                                        enemyBindSource.value = 'player';
                                        addLog(`${nextEnemy.name}  ${charmResult.bindDuration || 1} `, 'system', 'info');
                                    }
                                    // -12%- MVU
                                    if (charmResult.selfEnduranceDebuff) {
                                        applyTalentBuff('player', '__', { : charmResult.selfEnduranceDebuff }, 999);
                                    }
                                }
                                // 2
                                if (charmResult.enemyGuaranteedHitCrit) {
                                    playerTalentState.value.lustEnemyGuaranteedCrit = true;
                                }
                            }
                        }
                        // buff/debuffapplySkillEffectsFromMvu
                        try {
                            const effectLogs = await applySkillEffectsFromMvu(skill.id, true);
                            effectLogs.forEach(log => addLog(log, 'system', 'info'));
                        }
                        catch (e) {
                            console.error('[] ', e);
                        }
                        // ==========  ==========
                        if (talentAttackResult.addBind && talentAttackResult.bindDuration && enemyBoundTurns.value === 0) {
                            // BOSS
                            if (BossSystem.bossState.isBossFight && BossSystem.bossState.bossId === 'muxinlan') {
                                addLog(`${nextEnemy.name} `, 'system', 'info');
                            }
                            else {
                                enemyBoundTurns.value = talentAttackResult.bindDuration;
                                enemyBindSource.value = 'player';
                                addLog(`${nextEnemy.name}  ${talentAttackResult.bindDuration} `, 'system', 'info');
                            }
                        }
                    }
                    // 
                    player.value = nextPlayer;
                    enemy.value = nextEnemy;
                    // MVU
                    saveToMvu();
                    // reloadStatusFromMvu
                    if (nextEnemy.stats.currentPleasure >= nextEnemy.stats.maxPleasure && turnState.climaxTarget === null) {
                        addLog(`${nextEnemy.name} `, 'system', 'critical');
                        // 
                        addLog(`${nextEnemy.name}  ()`, 'system', 'info');
                        triggerEffect('climax');
                        await processClimaxAfterLLM(true);
                    }
                    else {
                        // 
                        await reloadStatusFromMvu();
                        // 
                        setTimeout(handleEnemyTurn, 1000);
                    }
                }
                catch (e) {
                    console.error('[] ', e);
                    addLog('', 'system', 'critical');
                    turnState.phase = 'playerInput';
                }
            });
        }
        async function handlePlayerItem(item) {
            if (turnState.phase !== 'playerInput' || item.quantity <= 0) {
                if (item.quantity <= 0) {
                    addLog(`${item.name} `, 'system', 'info');
                }
                return;
            }
            // processing
            const nextPlayer = cloneCharacter(player.value);
            const nextEnemy = cloneCharacter(enemy.value);
            const itemIndex = nextPlayer.items.findIndex(i => i.id === item.id);
            if (itemIndex > -1) {
                nextPlayer.items[itemIndex].quantity -= 1;
                addLog(`${nextPlayer.name}  ${item.name}${nextPlayer.items[itemIndex].quantity}`, 'system', 'info');
            }
            const isSpecialNegativeItem = item.id === '' || item.id === '' || item.id === '';
            if (isSpecialNegativeItem) {
                const parts = [];
                if (typeof item.pleasureReduce === 'number' && item.pleasureReduce !== 0) {
                    const delta = -item.pleasureReduce;
                    if (delta > 0)
                        parts.push(`+${delta}`);
                    else
                        parts.push(`${delta}`);
                }
                if (typeof item.pleasureIncrease === 'number' && item.pleasureIncrease !== 0) {
                    parts.push(`+${item.pleasureIncrease}`);
                }
                if (item.bonuses && Object.keys(item.bonuses).length > 0) {
                    const bonusDesc = Object.entries(item.bonuses)
                        .map(([k, v]) => `${k}${v >= 0 ? '+' : ''}${v}`)
                        .join('');
                    parts.push(bonusDesc);
                }
                const summary = parts.length > 0 ? parts.join('') : '';
                addLog(` ${turnState.currentTurn} ${item.name} -> ${summary}`, 'system', 'info');
            }
            // ==================== buff/debuff ====================
            if (item.id === '') {
                try {
                    // 
                    playerBoundTurns.value = 0;
                    playerBindSource.value = null;
                    // buff/debuff
                    if (typeof Mvu !== 'undefined') {
                        const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                        if (mvuData?.stat_data) {
                            _.set(mvuData.stat_data, '.', {});
                            _.set(mvuData.stat_data, '.', {
                                : 0,
                                : 0,
                                : 0,
                                : 0,
                                : 0,
                                : 0,
                                : 0,
                                : 0,
                            });
                            await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                        }
                    }
                    addLog('buffdebuff', 'system', 'critical');
                    // 
                    player.value = nextPlayer;
                    enemy.value = nextEnemy;
                    activeMenu.value = 'main';
                    // 
                    turnState.phase = 'playerInput';
                    // MVUUI
                    await saveToMvu();
                    await reloadStatusFromMvu();
                    addLog(`---  ${turnState.currentTurn}  ---`, 'system', 'info');
                    return;
                }
                catch (e) {
                    console.error('[] ', e);
                    addLog('', 'system', 'critical');
                    // 
                    player.value = nextPlayer;
                    enemy.value = nextEnemy;
                    activeMenu.value = 'main';
                    await saveToMvu();
                    await reloadStatusFromMvu();
                    return;
                }
            }
            // ====================  ====================
            if (item.id === 'honor_medal_muxinlan') {
                if (!BossSystem.bossState.isBossFight || BossSystem.bossState.bossId !== 'muxinlan') {
                    addLog('', 'system', 'info');
                    // 
                    player.value = nextPlayer;
                    enemy.value = nextEnemy;
                    activeMenu.value = 'main';
                    await saveToMvu();
                    await reloadStatusFromMvu();
                    return;
                }
                if (BossSystem.bossState.currentPhase !== 1) {
                    addLog('', 'system', 'info');
                    // 
                    player.value = nextPlayer;
                    enemy.value = nextEnemy;
                    activeMenu.value = 'main';
                    await saveToMvu();
                    await reloadStatusFromMvu();
                    return;
                }
                if (BossSystem.bossState.hasUsedMedal) {
                    addLog('', 'system', 'info');
                    // 
                    player.value = nextPlayer;
                    enemy.value = nextEnemy;
                    activeMenu.value = 'main';
                    await saveToMvu();
                    await reloadStatusFromMvu();
                    return;
                }
                const ok = BossSystem.useHonorMedal();
                if (ok) {
                    addLog('', 'system', 'critical');
                }
                else {
                    addLog('', 'system', 'info');
                }
                // 
                player.value = nextPlayer;
                enemy.value = nextEnemy;
                activeMenu.value = 'main';
                await saveToMvu();
                await reloadStatusFromMvu();
                addLog(`---  ${turnState.currentTurn}  ---`, 'system', 'info');
                return;
            }
            // 
            const log = item.effect(nextPlayer, nextEnemy);
            addLog(log.message, log.source, log.type);
            // 
            if (item.staminaRestore) {
                addLog(`${nextPlayer.name} ${player.value.stats.currentEndurance}  ${nextPlayer.stats.currentEndurance}`, 'system', 'info');
            }
            if (item.pleasureReduce || item.pleasureIncrease) {
                addLog(`${nextPlayer.name} ${player.value.stats.currentPleasure}  ${nextPlayer.stats.currentPleasure}`, 'system', 'info');
            }
            // 
            player.value = nextPlayer;
            enemy.value = nextEnemy;
            // buffMVU
            if (item.bonuses && Object.keys(item.bonuses).length > 0 && typeof Mvu !== 'undefined') {
                const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                if (mvuData && mvuData.stat_data) {
                    // 
                    if (!mvuData.stat_data.)
                        mvuData.stat_data. = {};
                    if (!mvuData.stat_data..)
                        mvuData.stat_data.. = {};
                    if (!mvuData.stat_data..)
                        mvuData.stat_data.. = {};
                    // 
                    const buffDesc = item.description?.match(/(\d+)/);
                    const duration = buffDesc ? parseInt(buffDesc[1]) : 3;
                    // key
                    let statusKey = item.name;
                    let index = 1;
                    while (mvuData.stat_data..[statusKey]) {
                        statusKey = `${item.name}_${index}`;
                        index++;
                    }
                    // .
                    mvuData.stat_data..[statusKey] = {
                        : item.bonuses,
                        : duration,
                    };
                    // 
                    for (const [bonusKey, bonusValue] of Object.entries(item.bonuses)) {
                        mvuData.stat_data..[bonusKey] =
                            (mvuData.stat_data..[bonusKey] || 0) + bonusValue;
                    }
                    // MVU
                    await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                    addLog(`${item.name}  ${duration} `, 'system', 'info');
                }
            }
            // 
            activeMenu.value = 'main';
            // MVU/
            await saveToMvu();
            // MVUUI
            await reloadStatusFromMvu();
            // 
            // 
        }
        function handleEnemyTurn() {
            turnState.phase = 'enemyAction';
            console.info(`[]  - enemyBoundTurns=${enemyBoundTurns.value}, enemyBindSource=${enemyBindSource.value}`);
            // 
            if (playerBoundTurns.value > 0 && playerBindSource.value === 'enemy') {
                playerBoundTurns.value--;
                if (playerBoundTurns.value === 0) {
                    addLog(`${player.value.name} `, 'system', 'info');
                    playerBindSource.value = null;
                }
                else {
                    addLog(`${player.value.name}  ${playerBoundTurns.value} `, 'system', 'info');
                }
            }
            // 
            console.info(`[]  - enemyBoundTurns=${enemyBoundTurns.value}`);
            if (enemyBoundTurns.value > 0) {
                addLog(`${enemy.value.name}  ${enemyBoundTurns.value} `, 'system', 'info');
                // ========== BOSS -  ==========
                if (BossSystem.bossState.isBossFight &&
                    BossSystem.bossState.bossId === 'christine' &&
                    BossSystem.bossState.currentPhase === 2) {
                    const wrathPleasureGain = Math.floor(enemy.value.stats.maxPleasure * 0.2);
                    enemy.value.stats.currentPleasure = Math.min(enemy.value.stats.maxPleasure, enemy.value.stats.currentPleasure + wrathPleasureGain);
                    addLog(`${enemy.value.name} +${wrathPleasureGain}`, 'system', 'critical');
                }
                // ========== BOSS-1-1=-2 ==========
                if (BossSystem.bossState.isBossFight && BossSystem.bossState.bossId === 'eden') {
                    const countdownResult = BossSystem.processEdenTurnStart(enemyBoundTurns.value);
                    const isUrgent = countdownResult.countdownValue <= 3;
                    addLog(` ${countdownResult.countdownValue} -1`, 'system', isUrgent ? 'critical' : 'info');
                    // buff
                    if (!BossSystem.bossState.edenSleeping && BossSystem.bossState.edenAwakened) {
                        if (typeof Mvu !== 'undefined') {
                            const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                            if (mvuData?.stat_data) {
                                const tempStates = _.get(mvuData.stat_data, '..', {});
                                const awakeningBuff = tempStates[''];
                                if (awakeningBuff && awakeningBuff. && typeof awakeningBuff.. === 'number') {
                                    const oldValue = awakeningBuff..;
                                    const newValue = oldValue - 15;
                                    awakeningBuff.. = newValue;
                                    _.set(mvuData.stat_data, '...', awakeningBuff);
                                    let totalEnduranceBonus = newValue;
                                    Object.entries(tempStates).forEach(([name, state]) => {
                                        if (name !== '' && state?.?.) {
                                            totalEnduranceBonus += state..;
                                        }
                                    });
                                    _.set(mvuData.stat_data, '...', totalEnduranceBonus);
                                    const baseEndurance = _.get(mvuData.stat_data, '.', 0);
                                    const realTimeEndurance = Math.floor(baseEndurance * (1 + totalEnduranceBonus / 100));
                                    _.set(mvuData.stat_data, '.', realTimeEndurance);
                                    Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                                    enemy.value.stats.baseEndurance = realTimeEndurance;
                                    addLog(`${oldValue}%  ${newValue}%`, 'system', 'info');
                                }
                            }
                        }
                    }
                    // Game Over
                    if (countdownResult.triggerSkill16) {
                        addLog(``, 'system', 'critical');
                        BossSystem.queueDialogues(BossSystem.EDEN_DIALOGUES.countdown_zero);
                        // Game Over
                    }
                }
                // 
                enemyBoundTurns.value--;
                if (enemyBoundTurns.value === 0) {
                    enemyBindSource.value = null;
                    addLog(`${enemy.value.name} `, 'system', 'info');
                }
                endTurn().then(climaxTriggered => {
                    if (!climaxTriggered) {
                        setTimeout(startNewTurn, 1000);
                    }
                });
                return;
            }
            // ========== BOSS -  ==========
            if (BossSystem.bossState.isBossFight && BossSystem.bossState.bossId === 'eden') {
                // -1-1
                const countdownResult = BossSystem.processEdenTurnStart(enemyBoundTurns.value);
                // 
                const isUrgent = countdownResult.countdownValue <= 3;
                addLog(` ${countdownResult.countdownValue} `, 'system', isUrgent ? 'danger' : 'critical');
                // ========== buff-20 ==========
                if (!BossSystem.bossState.edenSleeping && BossSystem.bossState.edenAwakened) {
                    if (typeof Mvu !== 'undefined') {
                        const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                        if (mvuData?.stat_data) {
                            const tempStates = _.get(mvuData.stat_data, '..', {});
                            const awakeningBuff = tempStates[''];
                            if (awakeningBuff && awakeningBuff. && typeof awakeningBuff.. === 'number') {
                                // 15
                                const oldValue = awakeningBuff..;
                                const newValue = oldValue - 15;
                                awakeningBuff.. = newValue;
                                _.set(mvuData.stat_data, '...', awakeningBuff);
                                // 
                                let totalEnduranceBonus = newValue;
                                // debuff
                                Object.entries(tempStates).forEach(([name, state]) => {
                                    if (name !== '' && state?.?.) {
                                        totalEnduranceBonus += state..;
                                    }
                                });
                                _.set(mvuData.stat_data, '...', totalEnduranceBonus);
                                // 
                                const baseEndurance = _.get(mvuData.stat_data, '.', 0);
                                const realTimeEndurance = Math.floor(baseEndurance * (1 + totalEnduranceBonus / 100));
                                _.set(mvuData.stat_data, '.', realTimeEndurance);
                                Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                                // UIbaseEndurance
                                enemy.value.stats.baseEndurance = realTimeEndurance;
                                addLog(`${oldValue}%  ${newValue}%`, 'system', 'debuff');
                            }
                        }
                    }
                }
                // Game Over
                if (countdownResult.triggerSkill16) {
                    addLog(``, 'system', 'critical');
                    // Bug 3 Fix: 
                    BossSystem.queueDialogues(BossSystem.EDEN_DIALOGUES.countdown_zero);
                    // 
                    phaseTransitionEffect.value = 'eden-game-over';
                    // 2Game Over
                    setTimeout(async () => {
                        // Game Over_16
                        addLog(`Game Over`, 'system', 'critical');
                        addLog(`Game Over500%5`, 'system', 'damage');
                        // 500%  5 = 2500%
                        const gameOverDamage = Math.floor(enemy.value.stats.sexPower * 5.0 * 5);
                        player.value.stats.currentPleasure = Math.min(player.value.stats.maxPleasure, player.value.stats.currentPleasure + gameOverDamage);
                        addLog(`${player.value.name}  ${gameOverDamage} `, 'system', 'critical');
                        // 
                        setTimeout(() => {
                            phaseTransitionEffect.value = '';
                        }, 1500);
                        // Bug 4 Fix: 
                        if (player.value.stats.currentPleasure >= player.value.stats.maxPleasure) {
                            // 
                            player.value.stats.climaxCount++;
                            addLog(`${player.value.name} (${player.value.stats.climaxCount}/${player.value.stats.maxClimaxCount})`, 'system', 'climax');
                            triggerEffect('climax');
                            // 
                            if (player.value.stats.climaxCount >= player.value.stats.maxClimaxCount) {
                                // 
                                addLog(`${player.value.name} `, 'system', 'critical');
                                addLog(`${enemy.value.name} `, 'system', 'victory');
                                triggerEffect('defeat');
                                BossSystem.queueDialogues(BossSystem.EDEN_DIALOGUES.victory);
                                turnState.phase = 'gameOver';
                                return; // 
                            }
                            // 
                            player.value.stats.currentPleasure = 0;
                        }
                        // 
                        endTurn().then(climaxTriggered => {
                            if (!climaxTriggered) {
                                setTimeout(startNewTurn, 1000);
                            }
                        });
                    }, 2500); // 
                    return;
                }
                // 
                if (BossSystem.bossState.edenSleeping) {
                    addLog(`${enemy.value.name} ...`, 'system', 'info');
                    // 
                    const sleepDialogue = BossSystem.getEdenRandomBattleDialogue();
                    if (sleepDialogue) {
                        BossSystem.queueDialogues([sleepDialogue], false);
                    }
                    endTurn().then(climaxTriggered => {
                        if (!climaxTriggered) {
                            setTimeout(startNewTurn, 1000);
                        }
                    });
                    return;
                }
            }
            addLog(`${enemy.value.name} ...`, 'system', 'info');
            setTimeout(() => {
                // 
                if (!turnState.enemyIntention) {
                    // 
                    determineEnemyIntention();
                    addLog(`${enemy.value.name} `, 'system', 'info');
                }
                const nextPlayer = cloneCharacter(player.value);
                const nextEnemy = cloneCharacter(enemy.value);
                // 
                let skill = nextEnemy.skills.find(s => s.id === turnState.enemyIntention?.id);
                // 
                if (!skill || skill.currentCooldown > 0) {
                    const availableSkills = nextEnemy.skills.filter(s => s.currentCooldown === 0);
                    if (availableSkills.length > 0) {
                        skill = availableSkills[Math.floor(Math.random() * availableSkills.length)];
                        addLog(`${nextEnemy.name}  ${skill.name}`, 'system', 'info');
                    }
                    else {
                        // 
                        skill = nextEnemy.skills[0];
                        addLog(`${nextEnemy.name}  ${skill.name}`, 'system', 'info');
                    }
                }
                if (!skill) {
                    addLog(`${nextEnemy.name} `, 'system', 'info');
                    endTurn().then(climaxTriggered => {
                        if (!climaxTriggered) {
                            setTimeout(startNewTurn, 1000);
                        }
                    });
                    return;
                }
                // 
                const skillCost = skill.data?.staminaCost || skill.cost || 0;
                if (nextEnemy.stats.currentEndurance < skillCost) {
                    addLog(`${nextEnemy.name}  ${skill.name}`, 'system', 'info');
                    endTurn().then(climaxTriggered => {
                        if (!climaxTriggered) {
                            setTimeout(startNewTurn, 1000);
                        }
                    });
