        // LLM
        async function sendCombatLogToLLM(context) {
            try {
                const combatLogText = collectCombatLogs();
                const totalTurns = turnState.currentTurn;
                // 
                const isVictory = turnState.phase === 'victory';
                const resultText = isVictory ? '' : '';
                const contextText = isVictory ? '/' : '';
                // CG
                let fullPrompt = `${resultText}\n[]\n${combatLogText}\n${totalTurns}\n${contextText}`;
                // CG
                if (cgDescription.value) {
                    fullPrompt += `\n${cgDescription.value}`;
                }
                // 
                if (typeof createChatMessages === 'function') {
                    await createChatMessages([
                        {
                            role: 'user',
                            message: fullPrompt,
                        },
                    ]);
                }
                // LLM
                if (typeof generate === 'function') {
                    addLog('...', 'system', 'info');
                    const generatedText = await generate({ user_input: fullPrompt });
                    // AI
                    if (typeof createChatMessages === 'function') {
                        await createChatMessages([
                            {
                                role: 'assistant',
                                message: `[] ${generatedText}`,
                            },
                        ]);
                        // AI
                        // createChatMessages AI triggerSlash
                        addLog('LLMAI...', 'system', 'info');
                    }
                }
                else {
                    console.warn('[] generate');
                    addLog('generate', 'system', 'info');
                    //  generate AI
                    if (typeof triggerSlash === 'function') {
                        await triggerSlash('/trigger');
                    }
                }
            }
            catch (e) {
                console.error('[] LLM', e);
                addLog('LLM', 'system', 'critical');
            }
        }
        // 
        async function handleSendCombatLogToLLM() {
            const context = turnState.phase === 'victory' ? '' : turnState.phase === 'defeat' ? '' : '';
            await sendCombatLogToLLM(context);
            // 
            try {
                if (turnState.phase === 'victory' && typeof Mvu !== 'undefined') {
                    const resolvedEnemyName = resolveEnemyName(enemy.value.name).replace(/_\d+$/g, '');
                    if (resolvedEnemyName === '') {
                        const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                        if (mvuData?.stat_data) {
                            if (!mvuData.stat_data.)
                                mvuData.stat_data. = {};
                            if (!mvuData.stat_data..)
                                mvuData.stat_data.. = {};
                            const backpack = mvuData.stat_data..;
                            const itemKey = '';
                            if (!backpack[itemKey]) {
                                backpack[itemKey] = {
                                    : 'SS',
                                    : '',
                                    : '',
                                    : 1,
                                };
                                await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                                addLog(' 1', 'system', 'info');
                            }
                        }
                    }
                }
            }
            catch (e) {
                console.warn('[] ', e);
            }
            // 20%
            try {
                if (typeof Mvu !== 'undefined') {
                    const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                    if (mvuData && mvuData.stat_data) {
                        // 
                        const currentPleasure = _.get(mvuData.stat_data, '.$', 0);
                        const maxStamina = _.get(mvuData.stat_data, '.$', 100);
                        const currentStamina = _.get(mvuData.stat_data, '.$', 0);
                        // 
                        const newPleasure = Math.floor(currentPleasure / 2); // 
                        const staminaIncrease = Math.floor(maxStamina * 0.2); // 20%
                        const newStamina = Math.min(maxStamina, currentStamina + staminaIncrease); // 
                        // MVU
                        _.set(mvuData.stat_data, '.$', newPleasure);
                        _.set(mvuData.stat_data, '.$', newStamina);
                        // MVU
                        await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                        // 
                        addLog(` ${currentPleasure}  ${newPleasure} ()`, 'system', 'info');
                        addLog(` ${currentStamina}  ${newStamina} (+${staminaIncrease})`, 'system', 'info');
                        addLog('', 'system', 'info');
                        console.info(`[] : ${currentPleasure}  ${newPleasure}, : ${currentStamina}  ${newStamina} (+${staminaIncrease})`);
                    }
                }
            }
            catch (e) {
                console.error('[] ', e);
                addLog('', 'system', 'critical');
            }
            // 
            logs.value = [];
            // MVU
            try {
                if (typeof Mvu !== 'undefined') {
                    const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                    if (mvuData) {
                        _.set(mvuData.stat_data, '.', {});
                        await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                    }
                }
            }
            catch (e) {
                console.warn('[] ', e);
            }
        }
