                            _.set(mvuData.stat_data, '.', newEnemyData.);
                            _.set(mvuData.stat_data, '.', newEnemyData.);
                            _.set(mvuData.stat_data, '.', newEnemyData.);
                            _.set(mvuData.stat_data, '.', newEnemyData.);
                            _.set(mvuData.stat_data, '.', newEnemyData.);
                            _.set(mvuData.stat_data, '.', 0);
